@import "../../../style/index.scss";

.no-padding {
  padding: 0 0;
}
.padding {
  padding: 1% 12%;
}

.BountiesWrapper {
  @include leftcolumn;
  width: 100%;
  padding: 6% 10%;
  background-color: white;
  &__title {
    @include centeredrow;
    width: 100%;
    margin: 0 0 40px 0;
    h1 {
      font-size: 1.625rem;
      margin: 4px 0 0 0;
    }
    svg {
      width: 40px;
      margin: 0 10px 0 16px;
    }
  }
  &__desc {
    padding: 6% 0 2% 0;
    width: 100%;
    p {
      @include leftrow;
      max-width: 700px;
      margin: 0;
      @include sixteenpxfont;
      font-weight: 200;
    }
    a {
      @include rightrow;
      color: $background;
    }
    a:hover {
      color: $real-tellor-green;
    }
    a:visited {
      color: $background;
    }
  }
  &__collapse {
    background-color: white;
    padding: 3% 0;
    width: 100%;
    h3 {
      margin-top: 10px;
    }
    p {
      margin: 0;
      @include sixteenpxfont;
      font-weight: 200;
    }
    a {
      color: $background;
      &:hover {
        color: $real-tellor-green;
      }
    }
  }
}


.BountiesWrapperDark {
  @include leftcolumn;
  width: 100%;
  //padding: 1% 12%;
 
  background-color: $background;
  &__desc {
    padding: 6% 0 2% 0;
    width: 100%;
    p {
      @include leftrow;
      max-width: 700px;
      margin: 0;
      @include sixteenpxfont;
      font-weight: 200;
      color: white;
    }
    a {
      @include rightrow;
      color: white;
    }
    a:hover {
      color: $real-tellor-green;
    }
    a:visited {
      color: $background;
    }
  }
  &__collapse {
    background-color: $background;
    padding: 3% 0;
    width: 100%;
    h2 {
      color: white;
    }
    h3 {
      color: white;
      margin-top: 10px;
    }
    p {
      margin: 0;
      @include sixteenpxfont;
      font-weight: 200;
      color: white;
    }
    a {
      color: white;
      &:hover {
        color: $real-tellor-green;
      }
    }
  }
}
