@import '../../../style/index.scss';


.SampleUsing {
    @include leftcolumn;
    width: 100%;
    padding: 0.5% 12%;
    color: white;
    &__intro {
        h2 {
            max-width: 600px;
            max-height: 70px;
            line-height: 5rem;
            color: white;
            margin-bottom: 0%;
        }
        p {
            margin-top: 0%;
            max-width: 800px; 
            @include sixteenpxfont;
            font-weight: 200;
        }
    }
    &__video {
        padding: 3.5% 0;
        iframe {
            box-shadow: 0 0 15px 2px #111;
            width: 560px; 
            height: 315px;
            cursor:pointer;
        }
        @media (max-width: 810px) {
            iframe {
                width: 325px; 
                height: 157.5px;
            }
        }
    }
    &__actions {
        padding-top: 0.5%;
        display: flex;
        flex-direction: row;
        @include centeredrow;
        &__github {
            a {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
            }
            span {
                padding-left: 15px;
            }
        }
        @media (max-width: 810px) {
            @include centeredcolumn;
            padding: 20px 0;
            &__github  {
                padding: 40px 0;
            }
            button {
                margin-left: 20px;
            }
        }
    }


}