@import '../../../style/index.scss';

.ForSmart {
    @include leftcolumn;
    width: 100%;
    padding: 6% 12%;
    color: white;
    &__intro {
        h2 {
            max-width: 600px;
            max-height: 70px;
            line-height: 5rem;
            color: white;
            margin-bottom: 0%;
        }
        p {
            margin-top: 0%;
            max-width: 800px;
            @include sixteenpxfont;
            font-weight: 200;
        }
    }
    &__code {
        margin-top: 3.5%;
        code {
            width: 900px;
            background-color: #202020;
            padding: 20px;
            display: block;
        }
        .code-import{
            padding: 30px 40px;
        }
        .single-indent {
            padding: 0 40px;
            margin: 0px;
        }
        .double-indent {
            padding: 0 40px 0 60px;
            margin: 0px;
        }
        .triple-indent {
            padding: 0 40px 0 80px;
            margin: 0px;
        }
        .code-function {
            padding: 20px 40px 0px 40px;
            margin: 0px;
        }
        .ending-brace {
            padding: 0 40px 30px 40px;
            margin: 0px;
        }
        .highlight {
            position: absolute;
            background-color: #2DFC9F;
            width: 3px;
            height: 22px;
            left: 12%;
        }

    }
    &__actions {
        padding-top: 4.5%;
        display: flex;
        flex-direction: row;
        @include centeredrow;
        button {
            background-color: $background;
            font-size: 1rem;
            color: white;
            border: 1px solid white;
            border-bottom: 2px solid white;
            border-radius: 0;
            margin-right: 50px;
            cursor:pointer;
        }
    }
    @media (max-width: 810px) {
        &__code {
            code {
                width: 300px;
            }
        }
    }
}